@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,900&display=swap);
*,body,html{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;

}

body{
   background: -webkit-linear-gradient(left, #f2f2f2 0%, #f1f7ff 100%);
}

.header{
  background: -webkit-gradient(linear, left top, right top, from(#db4437), to(#dd5537));
  background: linear-gradient(to right, #db4437 0%, #dd5537 100%);
  padding: 50px 0;
  text-align: center;
  color: white;
}

.header h1{
  cursor: pointer
}

.rotate{
  -webkit-animation:spin 1s linear infinite;
  animation:spin 1s linear infinite;
}
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }


.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  opacity: 1;
  -webkit-transition: all 900ms;
  transition: all 900ms;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  -webkit-transition: all 600ms;
  transition: all 600ms;
}

